import React, { useState } from 'react';
import './Basic.css';

function Basic({basic}) {
    const [caches, setCaches] = useState([]);
    const [newlist, setNewlist] = useState("")

    function equal(){
        const theInput = document.getElementById("theInput");

        if(theInput.value === "Syntax Error") setNewlist("")
        else if (theInput.value.length > 0) {
            
            let numbers = theInput.value.split(/[\u00F7\u00D7%+^-]/g).filter(e=>e!=="")
            let operators = theInput.value.split(/[0-9]|[.]/g).filter(e=>e!=="")
            let equateList = []
       
            if(!theInput.value[0].match(/[%^+\u00F7\u00D7-]/g)){
                for (var i = 0; i < numbers.length; i++){
                    equateList.push(numbers[i])
                    equateList.push(operators[i])
                }}
            else{
                for (var j = 0; j < operators.length; j++){
                    equateList.push(operators[j])
                    equateList.push(numbers[j])
                }}
            
            let filteredList = equateList.filter(e => e !== undefined && e !== "")
            let filteredList2 = equateList.filter(e => e !== undefined && e !== "")
            
            for (var k = 0; k < filteredList.length; k++){
                if (k === 0 && filteredList[0] === "-") {
                    filteredList[1] = filteredList[0] + filteredList[1]
                    filteredList[0] = ""
                }
                else if (k === 0 && filteredList[0].match(/[%^+\u00F7\u00D7]/g)) {
                    filteredList[0] = ""
                }
                if (filteredList[k].length > 1) {
                    if (filteredList[k].includes("-") && !filteredList[k].match(/[0-9]|[.]/g)) {
                        if(filteredList[filteredList.length-1].match(/[0-9]|[.]/g)){
                            filteredList[k+1] = filteredList[k][filteredList[k].indexOf("-")] + filteredList[k+1]
                            filteredList[k] = filteredList[k][0]
                        }
                        else filteredList[k] = ""
                    }
                    if (!filteredList[k].match(/[0-9]/g)) {
                        if(filteredList[filteredList.length-1].match(/[0-9]|[.]/g))
                            filteredList[k] = filteredList[k][0]
                        else filteredList[k] = ""
                    }  
                } 
            }
            filteredList = filteredList.filter(e=>e!=="")

            let flnum = filteredList.filter(e => e.match(/[0-9]/i)).map(e => +e)
            let flop = filteredList.filter(e => !e.match(/[0-9]/i))

            if (theInput.value.includes("^")) {
                while (flop.includes("^")) {
                    const index = flop.indexOf("^")
                    flnum[index] = flnum[index] ** flnum[index+1]
                    flnum[index+1] = ""
                    flop[index] = ""
                }
            }
            if (theInput.value.includes("%")) {
                while (flop.includes("%")) {
                    if (filteredList[filteredList.indexOf("%") - 1] && filteredList[filteredList.indexOf("%") + 1]) {
                        const index = flop.indexOf("%")
                        flnum[index] = flnum[index] % flnum[index+1]
                        flnum[index+1] = ""
                        flop[index] = ""
                    }
                }
            }
            flop = flop.filter(function(e){ return e!==""})
            flnum = flnum.filter(function(e){ return e!==""})
            
            var equalize = flnum.toString().length >= 1 ? flnum[0] : parseInt(flnum);
            for (var l = 0; l <= flop.length; l++) {
                if(flop[l]==="+") equalize += flnum[l+1];
                if(flop[l]==="-") equalize -= flnum[l+1];
                if(flop[l]==="\u00D7") equalize *= flnum[l+1];
                if(flop[l]==="\u00F7") equalize /= flnum[l+1];
                if (flop[l] === "%") {
                    if(!filteredList[filteredList.length-1].match(/[0-9]|[.]/g))
                        equalize *= 0.01;
                    else equalize %= flnum[l+1];
                }  
                
            }

            setCaches([...caches,
                {
                    input: !isNaN(equalize) ? filteredList.join(""):filteredList2.join(""),
                    sumnumber: !isNaN(equalize) ? (parseFloat(equalize.toFixed(4))) : "Syntax Error",
                }
            ])

            setNewlist(!isNaN(equalize) ? equalize : "Syntax Error");
                
            }
            
    }
    const enter = (e) => {
        switch (e.keyCode) {
            case 191: e.target.value += "\u00F7"; break
            case 56: if(e.shiftKey) e.target.value += "\u00D7"; break
            case 13: equal(); break
            default: break
        }
    }
    const addtoInput = (e) => {
        const theInput = document.getElementById("theInput");
        theInput.value += e.target.value;
        setNewlist(theInput.value);
    } 

    const clear = (e) => {
        e.preventDefault();
        setNewlist("")
    } 

    const OnlyNumericalValue = (e) => {
        e.target.value = e.target.value.replace(/[^0-9%^.+\u00F7\u00D7-]/g, '')
    }
    const inputHandlre = (e) => {
        setNewlist(e.target.value);
    }
    const getInput = (e) => {
        if(e.target.textContent !== "Syntax Error"){
            const theInput = document.getElementById("theInput");
            theInput.value = e.target.textContent
        }
    }

    return (
        <div className="basic__container" style={{display: basic ? "" : "none"}}>
            <div className="equation__list">
            {
            caches.map((cache, i) => (
                <div key={i} className="equation">
                    <h1 onClick={getInput}>{cache.input}</h1>
                    <h1>=</h1> 
                    <h1 onClick={getInput}>{cache.sumnumber}</h1>
                </div>
            ))
            }
            </div>
            <input type="text" id="theInput"
                value={newlist}
                onChange={inputHandlre}
                onKeyDown={enter}
                onInput={OnlyNumericalValue}
                autoComplete="off"
            />

            <div className="buttons">
            <button value="0" style={{gridArea:"n0"}} onMouseUp={addtoInput}>0</button>
            <button value="1" style={{gridArea:"n1"}} onMouseUp={addtoInput}>1</button>
            <button value="2" style={{gridArea:"n2"}} onMouseUp={addtoInput}>2</button>
            <button value="3" style={{gridArea:"n3"}} onMouseUp={addtoInput}>3</button>
            <button value="4" style={{gridArea:"n4"}} onMouseUp={addtoInput}>4</button>
            <button value="5" style={{gridArea:"n5"}} onMouseUp={addtoInput}>5</button>
            <button value="6" style={{gridArea:"n6"}} onMouseUp={addtoInput}>6</button>
            <button value="7" style={{gridArea:"n7"}} onMouseUp={addtoInput}>7</button>
            <button value="8" style={{gridArea:"n8"}} onMouseUp={addtoInput}>8</button>
            <button value="9" style={{gridArea:"n9"}} onMouseUp={addtoInput}>9</button>    
            
            <button value="." style={{gridArea:"period"}} onMouseUp={addtoInput}>.</button>
            <button value="%" style={{gridArea:"percent"}} onMouseUp={addtoInput}>%</button>
            <button value={"\u00F7"} style={{gridArea:"division"}} onMouseUp={addtoInput}>/</button>
            <button value={"\u00D7"} style={{gridArea:"multiple"}} onMouseUp={addtoInput}>x</button>
            <button value="-"style={{gridArea:"minus"}} onMouseUp={addtoInput}>-</button>
            <button value="+" style={{gridArea:"addition"}} onMouseUp={addtoInput}>+</button>
            <button style={{gridArea:"equal"}} onMouseUp={equal}>=</button>
            <button style={{gridArea:"clear"}} onMouseUp={clear}>C</button>
            </div>
        </div>
    )
}

export default Basic
