import React from 'react'
import "./Converter.scss"
import "./Converter.css"

function Binary({id, binaries, setBinaries, setValue, mode}) {

    const btnHandlre = e => {
        let newArr = [...binaries]
        if (e.target.value === "0") {
            newArr[id] = 1; setBinaries(newArr); 
        }
        else {
            newArr[id] = 0; setBinaries(newArr); 
        }
    }

    const btnHandlre2 = e => {
        e.preventDefault()
        if(mode)
            setValue(parseInt((binaries.join("").split("").reverse().join("")), 2))
        else
            setValue(parseInt((binaries.join("").split("").reverse().join("")), 2).toString(16).toUpperCase())
    }

    return (
        <div className="btnbtn">
            <button value={binaries[id]} className="btn" onMouseDown={btnHandlre2} onMouseDownCapture={btnHandlre}>
            {binaries[id]}</button>
            
        </div>
    )
}

export default Binary
