import React,{useState} from 'react'
import Binary from "./Binary"
import "./Converter.scss"

function Converter({converter}) {
    let btn = []
    let setter = []
    const [value, setValue] = useState("0")
    const [binaries, setBinaries] = useState(setter)
    const [mode, setMode] = useState(true)

    for (var i = 0; i < 48; i++){
        btn.push(i)
        setter.push(0)
    }



    const inputHandlre = e => {
        setValue(e.target.value.length <=1 && !mode ? "0" : e.target.value.toUpperCase())
        let binarify = mode ? parseInt(e.target.value).toString(2) : parseInt(e.target.value,16).toString(2)
        let binarifyR = binarify.split("").reverse()
        let newArr = [...binaries]
        if(parseInt(e.target.value) >= 0 && (mode ? e.target.value.length <= 15 : e.target.value.length <= 13)){
        for (let i = 0; i < binarify.length; i++){
            newArr[i] = binarifyR[i]
            setBinaries(newArr)
        }
        for (let j = binarify.length; j < 48; j++){
            newArr[j] = 0
            setBinaries(newArr)
        }}
    }

    
    window.onclick = function (event) {
        if (!event.target.matches('.dropbtn')) {
        var dropdowns = document.getElementsByClassName("dropdown-content");
        for (let i = 0; i < dropdowns.length; i++) {
            var openDropdown = dropdowns[i];
            if (openDropdown.classList.contains('show'))
                openDropdown.classList.remove('show');
        }
    }
    if (event.target.matches('.dec')) {
        setMode(true)
        setValue(parseInt((binaries.join("").split("").reverse().join("")), 2))
    }
    else if (event.target.matches('.hex')) {
        const conInput = document.getElementById("converterInput")
        setMode(false)
        let x = parseInt((binaries.join("").split("").reverse().join("")), 2).toString(16).toUpperCase()
        setValue(conInput.value ==="0" || conInput.value ==="" ? x : "0" + x)
    }
  }
    

    const OnlyHexValue = (e) => {
        e.target.value = e.target.value.replace(/[^0-9a-fA-F]/g, '')
    }
    function myFunction() {
        document.getElementById("myDropdown").classList.toggle("show");
    }
  
    return (
        <div className="converter" style={{display: converter ? "" : "none"}}>
            <div className="button_container">
            {
                btn.map((item, i) => (
                    <Binary key={item} id={i}
                        binaries={binaries} setBinaries={setBinaries}
                        value={value}
                        setValue={setValue}
                        setMode={setMode}
                        mode={mode}
                    />
                ))
                
                }
            </div>
            <div className="marker">
                <h1>0</h1>
                <h1>12</h1>
                <h1>23</h1>
                <h1>24</h1>
                <h1>36</h1>
                <h1>47</h1>
            </div>
            <input type={mode ? "number":"text"} id="converterInput"
                autoComplete="off"
                spellCheck="false"
                value={value}
                onChange={inputHandlre}
                onInput={OnlyHexValue}
            />
            <div className="dropdown">
                <button onClick={myFunction} className="dropbtn">{mode ? "Decimal":"Hexadecimal"}</button>
                <div id="myDropdown" className="dropdown-content">
                    <p className="dec">Decimal</p>
                    <p className="hex">Hexadecimal</p>
                    {/* <p className="contact">Contact</p> */}
                </div>
            </div>
        </div>
    )
}

export default Converter
